@import url('https://fonts.googleapis.com/css2?family=Jost:wght@400;700&display=swap');

.box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.greybox {
  width: 172px;
  height: 32px;
  background-color: rgba(217, 217, 217, 0.5);
  display: flex;
  flex-direction: column;
  align-items: center;
  border-left: 30%;
  border-right: 30%;
}

.name {
  font-family: 'Jost';
  font-weight: 800;
  font-size: 19px;
  line-height: 0.9em;
  //text-shadow: 0.1px 0.1px 0.1px black;
}

.tag {
  opacity: 0.7;
  font-style: italic;
  font-size: 13px;
}

.bot {
  margin-top: 2%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav {
  font-family: 'Jost';
  padding-left: 6%;
  padding-right: 6%;
  cursor: pointer;
  opacity: 0.5;
  transition: opacity 0.5s;
}

.nav:hover {
  opacity: 1;
}
